.stars {
  display: flex;

  &__star {
    display: flex;
    height: 16px;
    width: 16px;
    background-image: url(../images/star.svg);
    background-position: center;
    background-repeat: no-repeat;

    &:not(:last-child) {
      margin-right: 4px;
    }
  }

  @for $stars from 1 through 5 {
    &--#{$stars} > :nth-child(-n + #{$stars}) {
      background-image: url(../images/star-active.svg);
    }
  }
}
